<template>
    <div id="guideSummaryShow">
        <h3 class="form_title">{{ paramsData.name }}</h3>
        <el-form ref="form">
            <table class="form_inner_table">
                <thead>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td style="padding:0 97px;"></td>
                        <td style="padding:0 65px;"></td>
                        <td style="padding:0 75px;"></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td style="padding:0 111px;"></td>
                        <td style="padding:0 95px;"></td>
                        <td style="padding:0 95px;"></td>
                    </tr>
                </thead>
                <tbody>
                    <tr class="classify_title" v-show="showTable1.length > 0">
                        <td colspan="21">{{ showTable1.length }}</td>
                    </tr>
                    <tr v-for="(item,index) in showTable1" :key="" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                    <tr class="classify_title" v-show="showTable2.length > 0">
                        <td colspan="21">
                            <span v-show="showTable1.length == 0"></span>
                            <span v-show="showTable1.length > 0"></span>{{ showTable2.length }}
                        </td>
                    </tr>
                    <tr v-for="(item,index) in showTable2" :key="index" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                    <tr class="classify_title" v-show="showTable3.length > 0">
                        <td colspan="21">
                            <span v-show="showTable1.length == 0 && showTable2.length == 0"></span>
                            <span v-show="(showTable1.length == 0 && showTable2.length > 0) || (showTable1.length > 0 && showTable2.length == 0)"></span>
                            <span v-show="showTable1.length > 0 && showTable2.length > 0"></span>{{ showTable3.length }}
                        </td>
                    </tr>
                    <tr v-for="(item,index) in showTable3" :key="index" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                </tbody>
            </table>
        </el-form>
        <div class="btn_group">
            <el-button class="btn_back" @click="handleBack"></el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'guideSummaryShow',
        data() {
            return {
                paramsData: {
                    name: this.$route.params.id
                },
                chineseIndex: ['','',''],
                showTable1: [],
                showTable2: [],
                showTable3: []
            }
        },
        methods: {
            handleBack() {
                this.$router.go(-1);
            }
        },
        mounted() {
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getSummaryByTitle',
                method: 'get',
                params: {
                    guideSummaryTitle: this.paramsData.name
                }
            }).then((res) => {
                let data = res.data.data;
                for(let i in data) {
                    let category = data[i].ownership_category;
                    if(category == 76) {          //
                        this.showTable1.push(data[i])
                    }else if(category == 77) {    //
                        this.showTable2.push(data[i])
                    }else if(category == 78) {    //
                        this.showTable3.push(data[i])
                    }
                }
            })
        }
    }
</script>

<style lang="less">
    #guideSummaryShow {
        min-height: 100%;
        background-color: #fff;
        overflow-y: hidden;
        padding: 0 20px;
        .el-form {
            height: 100%;
            overflow-x: auto;
            .form_inner_table {
                tbody {
                    .classify_content {
                        .text_center {
                            text-align: center;
                        }
                        td {
                            white-space: nowrap;
                            padding: 0 10px;
                        }
                    }
                }
            }
        }
        .btn_group {
            margin: 20px 0;
        }
    }
</style>



