<template>
    <div id="progressReportShow">
        <div class="showForm">
            <el-form ref="showForm" >
                <table class="form_table" :model="showForm">
                    <thead>
                        <tr><th colspan="4"></th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td></td>
                            <td>
                                <el-input
                                    v-model="showForm.subjectCategory"
                                    :disabled="true">
                                </el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input 
                                    v-model="showForm.projectNo"
                                    :disabled="true">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input 
                                 v-model="showForm.contractStartTime" 
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input 
                                 v-model="showForm.contractEndTime" 
                                 :disabled="true"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjeceLeader" 
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectLeaderPhone"
                                 :disabled="true">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>:</td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectContact"
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td></td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectContactPhone" 
                                 :disabled="true"
                                >
                            </el-input>
                            </td>
                        </tr>
                        <tr style="height:50px;">
                            <td></td>
                            <td colspan="3">
                                <el-radio v-model="radio" label="1"></el-radio>
                                <el-radio v-model="radio" label="2"></el-radio>
                                <el-radio v-model="radio" label="3"></el-radio>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <p>{{ taskForm.length }}</p>
                                <ol>
                                    <li v-for="(item,index) in taskForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <p>{{ progressForm.dataList.length }}{{ progressForm.progress }}%</p>
                                <ol>
                                    <li v-for="(item,index) in progressForm.dataList" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol style="list-style-type:none;margin-left:-15px;">
                                    <li>{{ fundsForm.num }}{{ fundsForm.num }}{{ fundsForm.num }}%</li>
                                    <li>{{ fundsForm.num }}{{ fundsForm.num }}%</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol>
                                    <li v-for="(item,index) in problemForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol>
                                    <li v-for="(item,index) in planForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <ol>
                                    <li>{{ timeForm.date }}</li>
                                    <li>
                                        
                                        <el-radio v-model="timeForm.radio" label="1"></el-radio>
                                        <el-radio v-model="timeForm.radio" label="2"></el-radio>
                                    </li>
                                    <li>{{ timeForm.date }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4></h4>
                                <el-input 
                                    v-model="showForm.reason_basis" 
                                    :disabled="true"
                                    :autosize="{ minRows: 10}"
                                    type="textarea">
                                </el-input>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <el-button type="primary" @click = "handleBack"></el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name:'progressReportShow',
        data(){
            return{
                radio: '',
                showForm: {
                },
                taskForm: [
                    '',
                    '',
                    ''
                ],
                fundsForm: {
                    num: 50
                },
                progressForm: {
                    progress: '60',
                    dataList: [
                        '',
                        '',
                        ''
                    ]
                },
                problemForm: [
                    '',
                    '',
                    ''
                ],
                planForm: [
                    '',
                    '',
                    ''
                ],
                timeForm: {
                    date: "2019824",
                    radio: ''
                },
                paramsData: {
                    id: this.$route.params.id
                }
            }
        },
        methods:{
            handleAudite:function(){
                const loading = this.$loading({
                    lock: true,
                    text: '...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'success',
                        callback: action => {
                            this.$router.go(-1);
                        }
                    });
                },2000);
            },
            handleNotAudite() {
                const loading = this.$loading({
                    lock: true,
                    text: '...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'success',
                        callback: action => {
                            this.$router.go(-1);
                        }
                    });
                },2000);
            },
            handleBack:function(){
                this.$router.go(-1);//
            }
        }

    }
</script>

<style lang="less">
    #progressReportShow{
        .showForm{
            table.form_table{
                min-width: 1000px;
                width: 65.4%;
                thead{
                    th{
                        // font-weight: bold;
                    }
                }
                @media screen and ( max-width: 1600px ) {
                    width: 87.4%;
                }
                .el-radio {
                    margin: 0 50px;
                }
                .item {
                    td {
                        * {
                            text-align: left;  
                        }
                        h4 {
                            padding: 15px 0;
                        }
                        background-color: #fff !important;
                        padding: 0 10px;
                        ol {
                            padding-bottom: 5px;
                            li {
                                margin-left: 20px;
                                line-height: 1.8;
                            }
                        }
                    }
                    .el-textarea {
                        margin-bottom: 10px;
                    }
                }
            }
            .qianding{
                padding: 10px 0;
                span{
                    line-height: 30px;
                }
                .el-input{
                    width: auto;
                    .el-input__inner{
                        line-height: 30px;
                        height: 30px;
                    }
                }
            }
        }
    }
</style>



