<script>
    export default {
        name: 'contractCenterShow',
        data() {
            return {
                showForm: {},
                progressForm:[],
                unitForm:{},
                keyForm: [],
                budgetForm:{},
                paramsData: {
                    state: this.$route.params.state,
                    id: this.$route.params.id
                }
            }
        },
        methods:{
            handleAudite:function(){
                const loading = this.$loading({
                    lock: true,
                    text: '...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'success',
                        callback: action => {
                            this.$router.go(-1);
                        }
                    });
                },2000);
            },
            handleNotAudite() {
                const loading = this.$loading({
                    lock: true,
                    text: '...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'success',
                        callback: action => {
                            this.$router.go(-1);
                        }
                    });
                },2000);
            },
            handleBack:function(){
                this.$router.go(-1);//
            }
        },
        beforeMount() {
            // 
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/contract/getManageInfoById',
                method: 'get',
                params: {
                    id: this.paramsData.id
                }
            }).then((res) => {
                let data = res.data.data;
                this.showForm = data;
            }).catch(() => {})
            // 
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/contentindicators/getIndicatorById',
                method: 'get',
                params: {
                    id: this.paramsData.id
                }
            }).then((res) => {
                let data = res.data.data;
                this.progressForm = data;
            }).catch(() => {})
            // 
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/contract/subject_participa_unit/getDeveloperInfoById',
                method: 'get',
                params: {
                    id: this.paramsData.id
                }
            }).then((res) => {
                let data = res.data.data;
                this.unitForm = data;
            }).catch(() => {})
            // 
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/contract/keydev/getKeyDevInfoById',
                method: 'get',
                params: {
                    cid: this.paramsData.id
                }
            }).then((res) => {
                let data = res.data.data;
                this.keyForm = data;
            }).catch(() => {})
            // 
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/contract/subjectfundbudget/getInfoById',
                method: 'get',
                params: {
                    id: this.paramsData.id
                }
            }).then((res) => {
                let data = res.data.data;
                this.budgetForm = data;
            }).catch(() => {})
        }
    }
</script>

<style lang="less">
    #contractCenterShow{
        .showForm{
            table.form_table,
            table.form_table1,
            table.form_table2{
                min-width: 1000px;
                width: 65.4%;
                thead{
                    th{
                        font-weight: bold;
                    }
                }
                @media  screen and ( max-width: 1600px ) {
                    width: 87.4%;
                }
                
            }
            .qianding{
                padding: 10px 0;
                span{
                    line-height: 30px;
                }
                .el-input{
                    width: auto;
                    .el-input__inner{
                        line-height: 30px;
                        height: 30px;
                    }
                }
            }
        }
    }
</style>



