<template>
    <div id="midTermMessageBox">
        <!--  -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm" >
                <el-form-item label="" >
                    <el-input v-model="queryForm.category"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="queryForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                     <el-input v-model="queryForm.contacts"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="queryForm.unit"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="queryForm.department"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="queryForm.phone"></el-input>
                </el-form-item>
            </el-form>
            <el-button type="primary"></el-button>
        </div>

        <!--  -->
        <div class="showList">
            <!--  -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                    type="selection"
                    align="center">
                </el-table-column>
                <el-table-column
                    type="index"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label=""
                    :show-overflow-tooltip="true"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="region"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="category"
                    min-width="115"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="funds"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="date"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="unit"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="contacts"
                    label=""
                    align="center">
                </el-table-column>
            </el-table>
            <!--  -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange">
            </pages>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'midTermMessageBox',
        data() {
            return {
                selectedIDs: [],
                queryForm:{
                    name:'',
                    unit:'',
                    contacts:'',
                    phone:'',
                    region:'',
                    category:''
                },
                tableData:[{
                    id: '1001',
                    name: '11111111',
                    region: '',
                    category: '1',
                    funds:'',
                    date:'',
                    unit:"122222222222222222222",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1002',
                    name: '2',
                    region: '',
                    category: '2',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1003',
                    name: '3',
                    region: '',
                    category: '3',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1004',
                    name: '4',
                    region: '',
                    category: '4',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1005',
                    name: '5',
                    region: '',
                    category: '5',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1006',
                    name: '6',
                    region: '',
                    category: '6',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1007',
                    name: '7',
                    region: '',
                    category: '7',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1008',
                    name: '8',
                    region: '',
                    category: '8',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                },{
                    id: '1009',
                    name: '9',
                    region: '',
                    category: '9',
                    funds:'',
                    date:'',
                    unit:"1",
                    contacts:"1",
                    phone:"17856567788"
                }],
                multipleSelection: [],
                currentPage: 4,
                fenye: {
                    total:400, //
                    pageNum:1,
                    pageSize:10, //
                    pageSizes:[10,20,30,40,50] //
                }
            }
        },
        methods:{
            handleSelectionChange(val) {
                this.multipleSelection = val;
                let ids = [];
                this.multipleSelection.map((item)=> {
                    ids.push(item.id);
                })
                this.selectedIDs = ids;
                this.$emit("receipt",this.selectedIDs);
            },
            handleCurrentChange:function(val){  //val
                console.log(val)
            },
            handleSizeChange(val){              //val
                console.log(val)
            }
        }
    }
</script>

<style lang="less">
    #midTermMessageBox{
        background-color: #edf1f4;
        * {
           font-size: 14px; 
        }
        .queryForm {
            .el-form {
                padding: 5px 0 0 0;
                height: 90px;
                .el-form-item {
                    margin: 5px 0;
                    label {
                        width: 110px !important;
                    }
                }
            }
            .el-button {
                bottom: 10px;
                right: 35px;
            }
        }
        .showList {
            position: relative;
            height: 360px;
            min-height: 0 !important;
            background-color: #fff;
            margin-top: 5px;
            padding: 5px;
        }
        .el-table {
            min-height: 0 !important;
            padding-bottom: 0 !important;
            .has-gutter {
                width: 99%;
            }
            .el-table__body-wrapper {
                height: 315px;
                overflow-y: auto;
            }
            table {
                width: auto !important;
            }
            .el-table--scrollable-x,.el-table__body-wrapper{
                overflow-x: hidden;
            }
            &::before {
                background-color: #fff;
                height: 0;
            }
            td,th {
                padding: 6px 0;
            }
            td,th {
                border-left: 1px solid #EBEEF5;
                &:first-child{
                    border-right: 0;
                }
            }
        }
        .pages{
            padding-top: 5px;
            border-top: 1px solid #d9d9d9;
            .el-button{
                background-color: transparent;
                border: 0;
                margin: 0;
                span{
                    line-height: 1;
                }
            }
        }
    }
</style>


