<template>
    <div id="progressReportList">
        <!--  -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm">
                <el-form-item label="" >
                    <el-input v-model="queryForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="queryForm.unit"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="queryForm.region" placeholder="">
                        <el-option
                                v-for="item in queryForm.reginOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="queryForm.category">
                        <el-option 
                                v-for="item in queryForm.categoryOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <el-button></el-button>
        </div>

        <!--  -->
        <div class="showList">
            <!--  -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                    type="selection"
                    align="center">
                </el-table-column>
                <el-table-column
                    type="index"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label=""
                    :show-overflow-tooltip="true"
                    align="center">
                    <template slot-scope="scope">
                        <router-link :to="{
                            name: 'ProgressReportShow',
                            params: {
                                id: scope.row.id
                            }
                        }"> 
                        {{ scope.row.name }}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="danwei"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="state"
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        <span class="orange" v-show="scope.row.state == '0'"></span>
                        <span class="green" v-show="scope.row.state == '1'"></span>
                        <span class="red" v-show="scope.row.state == '2'"></span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="time1"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="time2"
                    label=""
                    align="center">
                </el-table-column>
            </el-table>
            <!--  -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange">
            </pages>
        </div>
    </div>
</template>

<script>
    export default {
        name:'progressReportList',
        data() {
            return {
                queryForm: {
                    name:'',
                    unit:'',
                    contacts:'',
                    phone:'',
                    reginOptions:[{
                        value:'0',
                        label:'1'
                    },{
                        value:'1',
                        label:'2'
                    }],
                    region:'',
                    categoryOptions:[{
                        value:'0',
                        label:''
                    },{
                        value:'1',
                        label:''
                    }],
                    category:''
                },
                tableData: [{
                    id: '1001',
                    name: '11111111',
                    danwei: '1',
                    state: '0',
                    time1: '2019-08-14',
                    time2: '2019-08-15'
                },{
                    id: '1002',
                    name: '2',
                    danwei: '2',
                    state: '1',
                    time1: '2019-08-14',
                    time2: '2019-08-15'
                },{
                    id: '1003',
                    name: '3',
                    danwei: '3',
                    state: '0',
                    time1: '2019-08-14',
                    time2: '2019-08-15'
                },{
                    id: '1004',
                    name: '4',
                    danwei: '4',
                    state: '2',
                    time1: '2019-08-14',
                    time2: '2019-08-15'
                }],
                currentPage: 4,
                fenye: {
                    total: 400, //
                    pageNum: 1,
                    pageSize: 100, //
                    pageSizes: [100,30,40,50] //
                }
            }
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
                let ids = [];
                this.multipleSelection.map((item)=> {
                    ids.push(item.id);
                })
                this.selectedIDs = ids;
            },
            handleCurrentChange:function(val) {//val
                console.log(val)
            },
            handleSizeChange(val) {//val
                console.log(val)
            }
        }
    }
</script>

<style lang="less">
    #progressReportList {
        .queryForm {
            .el-form .el-form-item .el-form-item__label{
                width: 193px;
            }
        }
        .el-button{
            margin: 10px auto;
        }
        .showList {
            margin-top: 10px;
            min-height: 680px;;
            padding: 10px;
            .el-table {
                min-height: 630px;
                .orange {
                    color: #f2a50b;
                }
                .green {
                    color: #09bd90;
                }
                .red {
                    color: #fe0100;
                }
            }
            .pages {
                .el-button {
                    background-color: transparent;
                    border: 0;
                    margin: 0;
                    span {
                        line-height: 1;
                    }
                }
            }
        }
    }
</style>



